import React, { Component } from 'react'

export default class TodoInput extends Component {
    render() {
        const {item, type, handleChange, handleSubmit, editItem, handleSelect} = this.props  
        
        return (
            <div className="card card-body my-3">
                <form onSubmit={handleSubmit}>
                    <div className="input-group">
                        <div className="input-group-prepend">
                            <div className="input-group-text bg-info text-white">
                                <i className="fas fa-book" />
                            </div>
                        </div>

                        <input
                            type="text"
                            className="form-control"
                            placeholder="New Todo"
                            value={item}
                            onChange={handleChange}
                        />
                          <select class="form-control" value={type} onChange={handleSelect} >
                            <option value="Level">Level</option>
                            <option value="Do first">Do first</option>
                            <option value="Schedule">Schedule</option>
                            <option value="Delegate/Delay">Delegate/Delay</option>
                            <option value="Delete">Delete</option>
                          </select>
                    </div>

                    <button 
                        type="submit"
                        className={`btn btn-block mt-3 ${editItem ? 'btn-success' : 'btn-info'}`}
                    >
                        {editItem ? 'Edit task' : 'Add new task'}
                    </button>
                </form>
            </div>
        )
    }
}
